
import { defineComponent } from "vue";
import { mapState, mapGetters, mapActions } from "vuex";
import { formatTime } from "../utils/index";
import axios from "@/axios";
import { TransferBNB } from "../utils/pay";

//充值收款钱包
const ReceivePaymentAddress = "0x2b2cb0fD50f734a054Ea282D9C7fA8533B28C521";
//结束时间
const end_time = "2021/12/12 00:00:00";

export default defineComponent({
  name: "Home",
  data() {
    return {
      formatTime,
      format: "DD days HH:mm:ss",
      value: "",
      times: 0,
      max: "3", //最大金额
    };
  },
  setup: () => {
    return {};
  },
  components: {},
  computed: {
    ...mapState(["account", "presale", "user", "web3", "activeChainId"]),

    //end time
    endTime() {
      let endTime = new Date(end_time).getTime();
      let time = endTime - new Date().getTime();
      return time;
    },
    //acconut
    getAccount() {
      const { account } = this;
      const len = account.length;
      return account.slice(0, 5) + "..." + account.slice(len - 4, len);
    },
    //progress
    progress() {
      const { bought = 20, hardCap = 600 } = (this.presale as any) || {};
      if (bought >= hardCap) {
        return "100%";
      } else {
        return (bought / hardCap) * 100 + "%";
      }
    },
  },

  methods: {
    ...mapActions(["connectWallet"]),

    maxBtn() {
      this.value = this.max;
    },
    async buy() {
      try {
        if (/^[0-9]+$/.test(this.value)) {
          const address = await TransferBNB(
            this.web3,
            this.account,
            ReceivePaymentAddress,
            this.value
          );
          this.sendRecord();
        } else {
          return console.log("failed");
        }
      } catch (err) {
        console.log(err);
      }
    },

    sendRecord() {
      try {
        axios
          .post("/auth/recharge", {
            wallet: this.account,
            point: this.value,
          })
          .then((res) => {
            const { code } = res;
          });
      } catch (e) {
        console.log(e);
      }
    },
  },
});
